@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@500&display=swap');
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  user-select: none;
}
body {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 18px;
}
.container {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}
@media (min-width: 1400px) {
  .container {
    max-width: 1320px;
  }
}
.App .background {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 30%;
  z-index: -1;
}
@media (max-width: 991px) {
  .App .background {
    height: 40vh;
    width: 100%;
  }
}
.App .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 120px;
  min-height: 100vh;
}
@media (max-width: 1199px) {
  .App .container {
    gap: 80px;
  }
}
@media (max-width: 991px) {
  .App .container {
    flex-direction: column;
    gap: 100px;
  }
}
.App .left {
  flex-basis: 50%;
  display: flex;
  flex-direction: column;
  gap: 30px;
  order: -1;
}
@media (max-width: 991px) {
  .App .left {
    gap: 0;
    padding: 25px;
  }
}
.App .left .card {
  width: 450px;
  height: 250px;
  background-repeat: no-repeat;
}
.App .left .card.front {
  background-image: url(./bg-card-front.png);
  padding: 30px;
}
@media (max-width: 991px) {
  .App .left .card.front {
    order: 1;
    position: absolute;
    left: calc(50% - 50px);
    transform: translateX(-50%);
    top: 101px;
    z-index: 1;
  }
}
@media (max-width: 575px) {
  .App .left .card.front {
    top: 100px;
    left: calc(50% - 25px);
    transform: translateX(-50%);
  }
}
.App .left .card.front .card-number {
  margin-top: 50px;
  color: white;
  font-size: 30px;
  letter-spacing: 3px;
  text-align: center;
}
.App .left .card.front .info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
  color: white;
}
.App .left .card.back {
  background-image: url(./bg-card-back.png);
  align-self: flex-end;
}
@media (max-width: 991px) {
  .App .left .card.back {
    position: relative;
    right: -50px;
  }
}
@media (max-width: 575px) {
  .App .left .card.back {
    top: 0;
    left: calc(50% + 25px);
    transform: translateX(-50%);
  }
}
.App .right {
  flex-basis: 50%;
}
.App .right .row {
  margin-bottom: 60px;
}
.App .right .row:last-of-type {
  display: flex;
  gap: 30px;
}
.App .right .row:last-of-type > div:first-of-type div {
  display: flex;
  gap: 10px;
}
.App .right .row:last-of-type > div:first-of-type div input {
  width: 120px;
}
@media (max-width: 767px) {
  .App .right .row:last-of-type > div:first-of-type div input {
    width: 80px;
  }
}
.App .right .row:last-of-type > div:last-of-type {
  flex-basis: 100%;
}
.App .right .row label {
  text-transform: uppercase;
  margin-bottom: 10px;
  display: block;
  font-size: 14px;
  color: hsl(278, 68%, 11%);
  letter-spacing: 2px;
}
.App .right .row input {
  width: 100%;
  padding: 15px;
  border-radius: 8px;
  border: 1px solid hsl(270, 3%, 87%);
  transition: 0.3s;
}
.App .right .row input.warn {
  border-color: hsl(0, 100%, 66%);
}
.App .right .row input:focus {
  outline: none;
  border-color: hsl(278, 94%, 30%);
}
.App .right .row input::placeholder {
  font-size: 16px;
  transition: 0.3s;
}
.App .right .row input:focus::placeholder {
  opacity: 0;
}
.App .right .row .warning-msg {
  margin-top: 10px;
  font-size: 14px;
  color: hsl(0, 100%, 66%);
  position: absolute;
}
.App .right .submit {
  width: 100%;
  border: none;
  padding: 15px;
  border-radius: 8px;
  background-color: hsl(278, 68%, 11%);
  color: white;
  font-size: 20px;
  cursor: pointer;
  transition: 0.3s;
  margin-bottom: 25px;
  display: block;
}
.App .right .submit:hover {
  background-color: hsl(278deg 68% 11% / 80%);
}
.App .right .thank-you {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.App .right .thank-you svg {
  margin-bottom: 30px;
}
.App .right .thank-you h2 {
  margin-bottom: 10px;
  font-weight: 500;
  color: hsl(278, 68%, 11%);
}
.App .right .thank-you p {
  margin-bottom: 40px;
  color: gray;
}
